import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from '../environments/environment';

@Injectable({
  providedIn: 'root'
})
export class AdminServiceService {

//BaseUrl : string = "http://localhost:8080/";
BaseUrl : string = environment.baseUrl;
  
  constructor(private http : HttpClient) { 

  }

  login(obj){
    return (this.http.post(`${this.BaseUrl}admin/login`, obj));
    
  }

  getAllEmployee(){
   return (this.http.get(`${this.BaseUrl}admin/getAllEmployees`))
  }

  getEmployeeById(employeeId){
    return (this.http.get(`${this.BaseUrl}admin/getEmployee/${employeeId}`))
  }

  getAllCountries(){
    return (this.http.get(`${this.BaseUrl}country/getAllCountries`))
  }

  AddEmployee(obj){
    return (this.http.post(`${this.BaseUrl}admin/saveEmployee`,obj))
  }


  UpdateEmployee(obj){
    return (this.http.put(`${this.BaseUrl}admin/updateEmployee`,obj,{responseType: 'text'}))
  }

  deleteRecord(id){
    return (this.http.delete(`${this.BaseUrl}admin/deleteEmployee/${id}`, {responseType: 'text'}))
  }

  updateStatus(obj){
    return (this.http.put(`${this.BaseUrl}admin/updateStatus`,obj, {responseType: 'text'}))
  }

  forgetPassword(email){
    return (this.http.get(`${this.BaseUrl}admin/forgetPassword/${email}`, {responseType: 'text'}))
  }

}
//if String return from backend = ,{responseType:'text'}